import Vue from 'vue'
import axios from "axios";

// const URL = "/dev/api";
const URL = "http://api.guoxuekong.com/index.php/admin";
// const URL = "http://gx.lezchou.com/index.php/admin";
export const getUrl = URL;
const getAllParams = {
  app_type: process.env.NODE_ENV === 'admin_pc',// ioswebandroid, undefined, text,
  app_version: "1.0.0",// 1.0.0, , text,
  notify_id: 1231232,// id, , text,
  app_request_time: 100000,//  , , text,
  lang: "zh-cn",// zh-cnen, zh-cn, text,
  client_id: "by04esfH0glAa1",// id, , text,
  service_version: "100",// 100, 100, text,
  sign: "",
  uid: 0,
  sid: "",
  buss_data: "",
  country_no: "86"
}

//     ( + )
// POST
const log_in_params = {
  ...getAllParams,
  service_type: "by_UserLoginSession_loginByUsername",// , by_UserLoginSession_loginByUsername, text,
  username: "",//  6-24)
  device_token: "",// , , text,
  device_type: "",// (), , text,
  password: "",//8-24)
}

export const log_in = (data) => {
  return axios({
    method: "post",
    url: URL,
    data: { ...log_in_params,...data }
  })
}

